@media only screen and (max-width: 1000px) {
    .MainBox{
        width: 80%;
        height: auto;
    }
    #SkillsMainContainer{
        height: auto;
        width: 680px;
        /* border: 5px solid orange */
    }
    .cards{
        height: auto;
        width: auto;
        /* border: 5px solid rgb(252, 199, 100); */
    }
}

@media only screen and (max-width: 750px) {
    
    .MainBox{
        width: 80%;
        height: auto;
    }
    #SkillsMainContainer{
        width: 300px;
        height: auto;
        display: initial;
        /* border: 5px solid orange */

    }  
    .cards{
        margin: auto;
        margin-top: 10px;
        height:auto;
        width: 450px;
    }  
}
@media only screen and (max-width: 500px) {
    .MainBox{
        width: auto;
    }
    #SkillsMainContainer{
        width: 460px;
        height: auto;
        display: initial;
        /* border: 5px solid orange */

    }  
    .cards{
        margin: auto;
        margin-top: 10px;
        height:auto;
        width: 300px;
    }  
}