#image{
    display: block;
    margin-top: 20px;
    width:400px;
    /* color:  */
    border-radius: 40px;
    box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 5px rgb(238, 236, 236),
    2.3em 1.3em 1em rgba(0, 0, 0, 0.3);
}
#heading{
    text-shadow: 5px 5px #c3c0bf;
}
#text1{
    text-shadow: 5px 5px #c3c0bf;
}
#text2{
    text-shadow: 5px 5px #c3c0bf;
}

@media only screen and (max-width: 1450px) {
    #image{
        width: 370px;
    }  
    #greeting{
        height: 400px;
        width: 550px;
    }  
    #heading{
        font-size: 50px;
    }
    #text1{
        font-size: 30px;
    }
    #text2{
        font-size: 30px;
    }
}


@media only screen and (max-width: 1200px) {
    #image{
        width: 350px;
    }  
    #greeting{
        height: 350px;
        width: 450px;
    }  
    #img1{
       width: 90px;
    }
    #img2{
        width: 90px;
    }
    #heading{
        font-size: 50px;
    }
    #text1{
        font-size: 30px;
    }
    #text2{
        font-size: 30px;
    }
}

@media only screen and (max-width: 1000px) {
  
   
    #box{
        display: initial;
        height: 800px;
    }
    #image{
        display: block;
        margin: auto;
        margin-top: 20px;
        
    }
    #greeting{
        margin: auto;
        margin-top: 20px;
        width: auto;
        height: 400px;
        
    }    
}

@media only screen and (max-width: 500px) {
  
   
    #box{
        display: initial;
        height: 850px;
        margin-top: 30px;
    }
    #image{
        display: block;
        margin: auto;
        width: 300px;
        margin-top: 20px;
    }
    #greeting{
        height: 470px;
        width: auto;
        margin: auto;
        margin-top: 20px;
    }    
    #contact{
        margin:auto;
        margin-top: 10px;
    }
    #img1{
        width: 80px;
    }
    #img2{
        width: 80px;
    }
    #text1{
        font-size: 27px;
        width: 200px;
    }
    #text2{
        font-size: 27px;
    }
    #heading{
        font-size: 45px;
    }
}


