@media only screen and (max-width: 1000px) {
    
    .contactMainBox{
        height: 530px;
        width: auto;
    }
    #subBox{
        height: auto;
    }
    .contactCards{
        height: 300px;
        width: 250px;
    
    }
    #formId{
        margin-right: 20px;
        width: 100px;
        /* border: 1px solid white; */
        padding: 0px 0px 0px 0px ;
        
    }
    #resumeBtn{
        height: 50px;
        width: 200px ;
        margin-top: 0px;
    }
    #textArea{
        height: 50px;
        width: 200px;
        margin-right: 10px;
    }
    #email{
        width: 200px;
    }
    #Input1{
      width: 200px;
      margin-right: 10px;
    }
    #Input2{
      width: 200px;
      margin-right: 10px;
    }
    #Input3{
      width: 200px;
      margin-right: 10px;
    }
    
}
