    }
    #email{
        width: 200px;
    }
    #Input1{
      width: 190px;
      margin-right: 100px;
    }
    #Input2{
      width: 190px;
      margin-right: 100px;
    }
    #Input3{
      width: 190px;
      margin-right: 100px;
    }
   
    
}
@media only screen and (max-width: 650px) {
    
    .contactMainBox{
        height: auto;
        
    }
    #subBox{
        height: auto;
        display: initial;
        margin:auto;
        margin-top: 20px;
    }
    .contactCards{
        height: 350px;
        width: 150px;
        margin: auto;
        /* border: 1px solid white; */
    }
    #formId{
        margin-right: 20px;
        height: 250px;
        width: 150px;
        margin: auto;
        padding: 0px 40px 0 50px ;
        /* border: 1px solid white; */
        
    }
    #resumeBtn{
        height: 30px;
        width: 150px ;
        margin-top: 20px;
    }
    #textArea{
        height: 100px;
        width: 200px;
        margin-right: 100px;
    }
    #email{
        width: 200px;
    }
    #Input1{
      width: 200px;
      margin-right: 100px;
    }
    #Input2{
      width: 200px;
      margin-right: 100px;
    }
    #Input3{
      width: 200px;
      margin-right: 100px;
    }
    
}